import React, { useRef } from "react";

import "../load.scss";
function Load() {
    let loaderid, upid, downid, circle;
    loaderid = useRef();
    upid = useRef();
    downid = useRef();
    circle = useRef();

    function hide() {
        upid.current.style.transform = "translateY(-110%)";
        downid.current.style.transform = "translateY(100%)";
        loaderid.current.style.opacity = "0";
        circle.current.style.opacity = "0";
    }
    if (window.location.hash[window.location.hash.length - 1] !== "#") {
        window.location = window.location + "#";
        window.location.reload();
    }
    window.addEventListener("load", () => {
        hide();
    });

    return (
        <div class="loadStyle" ref={loaderid}>
            <div class="loadUp" ref={upid}></div>
            <div class="circleMiddle" ref={circle}>
                <div class="biggestCircle"></div>
                <div class="biggerCircle"></div>
                <div class="bigCircle"></div>
                <div class="mediumCircle"></div>
                <div class="smallCircle"></div>
            </div>
            <div class="loadDown" ref={downid}></div>
        </div>
    );
}

export default Load;
