import React, { useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";

function Icons(props) {
    let group = props.data.pathName;
    let [age, setAge] = useState();
    var interval = useRef;
    let timer = () => {
        let bornDate = new Date(`${props.data.birthday} 00:00:00`).getTime();
        interval = setInterval(() => {
            let now = new Date().getTime();
            let distance = now - bornDate;

            let year = Math.floor(distance / (1000 * 60 * 60 * 24 * 365.25));
            setAge(year);
        }, 1000);
    };
    useEffect(() => {
        timer();
    });
    useEffect(() => {
        window.addEventListener("scroll", scroll);
        function scroll() {
            let icon = document.getElementById(`${props.data.id}card`);
            let pos = icon.getBoundingClientRect().top;
            let top = window.innerHeight / 1.3;
            if (pos < top) {
                icon.classList.add("cardup");
            } else {
                icon.classList.remove("cardup");
            }
        }

        return () => window.removeEventListener("scroll", scroll);
    }, []);
    return (
        <>
            <div className="card-container">
                <Link
                    to={`/${group}/person/${props.data.id}`}
                    style={{ textDecoration: "none" }}
                >
                    <div className="card" id={`${props.data.id}card`}>
                        <img src={`${props.data.img}`} alt="icon" />
                        <p className="name">{props.data.name}</p>
                        <p>
                            <label className="bday">
                                {props.data.birthday}
                            </label>
                        </p>
                        <p>{age} years old</p>
                    </div>
                </Link>
            </div>
        </>
    );
}

export default Icons;
