import React from "react";

import KeplerBoys from "../../json/kepler/boys/kepler.json";
import KeplerGirls from "../../json/kepler/girls/kepler.json";
import KeplerImage from "../../json/kepler/keplerimage.json";

import Memories from "../memories.js";
import Icons from "../icons.js";
import Countdown from "../../countdown";
import Load from "../loading";

import "../../index.scss";
function Kepler() {
    return (
        <>
            {window.location.hash[window.location.hash.length - 1] !== "#" ? (
                <Load />
            ) : (
                ""
            )}
            <Memories img={KeplerImage} />
            <h1 className="titlekepler" id="boys">
                #Boys
            </h1>
            <div className="icon-container">
                {KeplerBoys.map((info, index) => (
                    <Icons data={KeplerBoys[index]} key={index} />
                ))}
            </div>
            <h1 className="titlekepler" id="girls">
                #Girls
            </h1>
            <div className="icon-container">
                {KeplerGirls.map((info, index) => (
                    <Icons data={KeplerGirls[index]} key={index} />
                ))}
            </div>
        </>
    );
}

export default Kepler;
